<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="section">
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="consumer-key"><span class="red-color">*</span> Consumer Key</label>
          <div class="slds-form-element__control">
            <input id="consumer-key" class="slds-input" type="text" v-model="settings.OAuthClientId" />
          </div>
        </div>
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="consumer-secret"><span class="red-color">*</span> Consumer Secret</label>
          <div class="slds-form-element__control">
            <input id="consumer-secret" class="slds-input" type="text" v-model="settings.OAuthSecret" />
          </div>
        </div>
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="callback-url"><span class="red-color">*</span> Callback URL</label>
          <div class="slds-form-element__control">
            <input id="callback-url" class="slds-input" type="text" v-model="settings.RedirectURI" />
          </div>
        </div>
      </div>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="cancel">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving || !canSave" @click="saveCharketAISettings">Save</button>
      </footer>
    </div>
  </right-page>
</template>

<script>
import { getCharketAISettings, saveCharketAISettings } from '../api/charket-settings'

export default {
  name: 'CharketAISettings',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Charket AI Settings'
      },
      settings: {
        OAuthClientId: '',
        OAuthSecret: '',
        RedirectURI: ''
      },
      isSaving: false,
      originalData: null
    }
  },
  computed: {
    hasChanged () {
      return JSON.stringify(this.originalData) !== JSON.stringify(this.settings)
    },
    canSave () {
      return this.settings.OAuthClientId && this.settings.OAuthSecret && this.settings.RedirectURI
    }
  },
  created () {
    this.fetchCharketAISettings()
  },
  methods: {
    async fetchCharketAISettings () {
      try {
        this.$bus.emit('show-loading')
        const data = await getCharketAISettings()
        this.originalData = data
        this.settings = JSON.parse(JSON.stringify(data))
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    cancel () {
      this.settings = JSON.parse(JSON.stringify(this.originalData))
    },
    async saveCharketAISettings () {
      try {
        this.isSaving = true
        await saveCharketAISettings(JSON.stringify(this.settings))
        this.$bus.emit('success', { content: 'Charket AI Settings was saved.', type: 'success' })
        this.originalData = JSON.parse(JSON.stringify(this.settings))
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .section {
    padding: 20px 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .form-section {
      width: 48%;
      margin-bottom: 10px;
      label {
        color: #706E6B;
      }
      .red-color {
        color: #c23934;
      }
    }
  }
</style>
